.searchBooking {
  padding: 15px;
  margin: 10px;
  border: 1px solid gray;
  border-radius: 10px;
  background-color: rgb(255, 177, 33);

  .btn {
    margin-top: 70px;
    width: 100%;
    height: 37px;
  }

  @media screen and (min-width: $tablet) {
    flex-basis: 250px;
    margin-right: 24px;
    padding: 10px;
  }

  @media screen and (min-width: $desktop) {
    flex-basis: 300px;
  }

  /* for mobile style */
  .form.mobile {
    @media screen and (max-width: $tablet) {
      height: 430px;
    }
  }

  /********* form__select only for mobile ***********/
  @media screen and (max-width: $tablet) {
    .form__select.is-hidden {
      visibility: hidden;
      opacity: 0;
      pointer-events: none;
      display: none;
    }
  }

  /* for tablet and desktop style */
  .form {
    @media screen and (min-width: $tablet) {
      height: 450px;
    }
  }

  /********* options_list **********/
  .options {
    position: relative;
  }

  .btn-outline-success {
    margin-top: 15px;
  }

  .options_list.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    display: none;
  }

  .options_list {
    visibility: visible;
    opacity: 1;
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    height: 330px;
    background-color: $colorwhite;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    padding: 20px;

    display: flex;
    flex-direction: column;

    .form-input {
      margin-bottom: 10px;
    }
    transition: all 550ms cubic-bezier(0.075, 0.82, 0.165, 1);
  }

  /* style element form */
  .form_item {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    position: relative;

    &:focus-within > .icon {
      fill: $background;
    }
  }

  .icon {
    position: absolute;
    top: 47%;
    left: 15px;
  }

  .form_label {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.01em;
    color: $colorp;
    margin-bottom: 4px;
  }

  .form-input {
    width: 100%;
    height: 40px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    padding-left: 40px;
    outline: none;

    &:focus {
      border: 1px solid $background;
    }
  }

  input {
    background-color: white;
    height: 10px;
  }

  .title {
    font-size: 20px;
    text-align: start;
    padding-top: 0;
    padding-bottom: 10px;
  }
}
